import React from 'react'
import Avatar from '../images/avatar.jpg'
import {AiOutlineDingtalk} from 'react-icons/ai'
import {TiArrowRightOutline} from 'react-icons/ti'
import Man from '../images/man.png'
import News from '../images/News.svg'
import Weather from '../images/Weather.png'

const Portfolio = () => {
    return (
        <div className='main-portfolio'>
        <div className='portfolio'>
         <h2>Hi, I'm <AiOutlineDingtalk size="45px" color='red' /><br/> Ivan Nikolic</h2>
         <h4>I am front-end developer,traveller,bla bla bla...</h4>
        </div><br/>
        <div className='avatar'>
        <img src={Avatar} alt='avatar' />
        <h6>    <span>React</span> <br/> is  my passion</h6>
        </div>
        <TiArrowRightOutline className='arrow' />
        <p style={{fontSize:'22px',marginLeft:'7px'}}>My projects</p>
        <div className='projects'>
            <div className='projects item'>
            <img src={News} alt='man' style={{opacity:'0.8',filter:' drop-shadow(0 0 0.75rem red)',width:'150px',height:'150px'}} />
                <p>News with APIs</p>
                <p className='title'>Html,Css,React,APIs</p>
                <p className='demo'>Demo:<a href='https://github.com/Ivann-2612/Zavrsni-projekat'>Online news app</a></p>
            </div>
            <div className='projects item'>
            <img src={Weather} alt='man' style={{opacity:'0.8',filter:' drop-shadow(0 0 0.75rem yellow)',width:'150px',height:'150px'}} />
                <p>Weather App</p>
                <p className='title'>Html,Css,React,API</p>
                <p className='demo'>Demo: <a href='https://ivann-2612.github.io/weather-app/'>Weather app</a></p>
            </div>
            <div className='projects item'>
            <img src={Man} alt='man' style={{opacity:'0.8',filter:' drop-shadow(0 0 0.75rem lime)',width:'150px',height:'150px'}} />
                <p>To-do list</p>
                <p className='title'>Html,Css,Javascript</p>
                <p className='demo'>Demo: <a href='https://ivann-2612.github.io/To-do-list/'>To-do app</a></p>
            </div>
        </div>
        </div>
    )
}

export default Portfolio

